    <BulletinBoard class="mb-2" style="min-height: 110px" :Bulletin="$store.state.bulletin.list" interval="5000" :loading="$store.state.bulletin.loading" />
    <div class="card">
      <div class="card-body">
        <button class="btn btn-success btn-relief px-5 py-2 font-weight-bolder mb-3" style="font-size: 30px;" @click="$router.push('/account/setting/qq-wechat')"> 🎁绑定QQ >> 领取新人五折券</button>
        <vs-alert success>
          <template #title> 详情 </template>
