        </vs-alert>

        <h4 class="mt-3 p-0 font-weight-bolder ps-3 border-left-primary border-left-width-3 text-nowrap">类型</h4>
        <hr />
        <XzChecks v-model="checked.version">
          <xz-option :value="0">
            <Card left>
                <div class=" px-4">
                    <div class="d-flex align-items-center">
                        <h4 class="font-weight-bolder mb-0 text-nowrap"> 普通版 </h4>
                        <span></span>
                    </div>
                    <div class="mt-2 text-secondary d-flex align-items-center">
                        <span class="badge me-1 py-1 badge-pill bg-success">
                            超划算
                        </span>
                    </div>
                </div>
            </Card>
          </xz-option>
          <xz-option :value="1" :disabled="true">
            <Card left color="danger" >
                <div class=" px-4">
                    <div class="d-flex align-items-center">
                        <h4 class="font-weight-bolder mb-0 text-nowrap"> 高级版 </h4>
                        <span></span>
                    </div>
                    <div class="mt-2 text-secondary d-flex align-items-center">
                        <span class="badge me-1 py-1 badge-pill bg-danger">
                            不支持
                        </span>
                    </div>
                </div>
            </Card>
          </xz-option>
        </XzChecks>
        <h4 class="mt-3 ps-3 font-weight-bolder border-left-primary border-left-width-3 text-nowrap">性能配置</h4>
        <hr />
        <XzChecks v-model="checked.performance">
          <xz-option :value="0">
            <Card left type="box">
                <div class=" px-2 ">
                    <div class="d-flex justify-content-between">
                      <h4 class="font-weight-bolder mb-0 text-nowrap"> CAC 高配版 </h4>
                      <div class="text-right">
                        <h3>
                          <span class="text-warning">{{ prices && prices.monthly_fee }} ¥</span>
                          <span class="font-medium-2">/月</span>
                        </h3>
                      </div>
                    </div>

                    <hr />
                    <div class="d-flex">
                      <h5 class="font-weight-bolder mb-0 text-nowrap text-secondary">长期维护 / 安全方便 / 高性能</h5>
                    </div>
                </div>
            </Card>
          </xz-option>
        </XzChecks>
        <h4 class="mt-3 ps-3 font-weight-bolder border-left-primary border-left-width-3 text-nowrap">应用配置</h4>
        <hr />

        <div class="ps-2">
          <h5 class="mt-3 ps-3 font-weight-bolder border-left-warning border-left-width-3 text-nowrap"><small class="text-danger">*</small> 应用信息 / 应用数据</h5>
          <hr />
