            <template #message-danger v-if="!app_info.app_name"> 必填项 </template>
          </vs-input>
          <vs-input class="mt-2" primary placeholder="班级名称" v-model="app_data.class_name">
            <template #message-danger v-if="!app_data.class_name"> 必填项 </template>
