          <div class="d-flex flex-column">
            <h4 class="m-0 p-0 font-weight-bolder text-nowrap">XzPay 正在保护您的支付安全</h4>  
            <p class="m-0 mt-2 text-secondary text-nowrap ">您正在使用 <b>XzPay</b> <span style="border-bottom: 2px dotted rgb(51, 153, 255);">作为您的首选支付方式</span></p>
          </div>
          
        </Card> 
      </div>
      
    </div>
    <BuyWrapper :discount="[{id: 1, title: '5折'}]" :computeFun="computePrice" :finishFun="startPay"></BuyWrapper>
  </div>
