<script>
import BuyWrapper from "@/components/BuyWrapper.vue";
import Card from "@/components/Card.vue";
import XzChecks from "@/components/XzChecks.vue";
import XzOption from "@/components/XzOption.vue";
import { buy_product, get_product_price } from "@/scripts/apps/product";

export default {
  data() {
    return {
      prices: null,
      checked: {
        version: 0,
        performance: 0,

        app_type: 1,
      },
      auto_renew_type: 1,
      auto_renew_status: 1,
      app_info: {
        app_name: '',
      },
      app_data: {
        class_name: '',
      }
    }
  },
  components: {
    Card,
    XzChecks,
    XzOption,
    BuyWrapper
  },
  async mounted() {
  
  },
  methods: {
    async getPrices() {
      const resp = await get_product_price(1001);
      if (resp.code !== 1) {
        this.$swal("获取价格失败", resp.msg, "error");
        return;
      }

      this.prices = resp.price;
    },
    async computePrice(auto_renew_type, discount) {
      if (!this.prices) {
        await this.getPrices();
      }

      this.auto_renew_type = auto_renew_type;
      // this.auto_renew_status = 1;

      let discountPrice = 0;
      let desc_price ='';

      if (auto_renew_type == 1) {
        discountPrice = this.prices.monthly_fee;
        desc_price = '¥/月';
      } else if (auto_renew_type == 2) {
        discountPrice = this.prices.annual_fee;
        desc_price = '¥/年';
      } else if (auto_renew_type == 3) {
        discountPrice = this.prices.lifetime_fee;
        desc_price = '¥/终身';
      }

      if (discount) {

      }

      desc_price = discountPrice + desc_price

      return {
        price: discountPrice,
        desc_price,

      };
    },
    startPay(price) {
      // config 验证
      if (!this.app_info.app_name || !this.app_data.class_name) {
        this.$swal({
          icon: "error",
          title: "操作失败",
          text: "应用配置-必填项未填写",
          confirmButtonText: "确认",
        })
        return;
      }

      this.$swal({
        icon: "warning",
        title: "确认支付",
        text: "您确定要支付 " + price + " 元并开通自动续费？",
        showCancelButton: true,
        confirmButtonText: "确认",
        cancelButtonText: "取消",
      }).then(async ({isConfirmed}) => {
        if (isConfirmed) {

          // 支付逻辑
          const resp = await buy_product(1001, {
            auto_renew_type: parseInt(this.auto_renew_type),
            auto_renew_status: parseInt(this.auto_renew_status),
            config: {
              app_type: parseInt(this.checked.app_type),
              app_name: this.app_info.app_name,
              class_name: this.app_data.class_name
            }
          })
          if (resp.code !== 1) {
            this.$swal({
              icon: "error",
              title: "操作失败",
              text: resp.msg,
              confirmButtonText: "确认",
            }).then(() => {
              if (resp.code === -4) {
                this.$router.push('/dashboard/expense/pay')
              }
            })
            return;
          } else {
            this.$swal({
              icon: "success",
              title: "购买成功",
              text: resp.msg,
              confirmButtonText: "确认",
            }).then(() => {
              this.$router.push('/apps/cac/list')
            })
          }
        }
      })
    }
  }
};
</script>

