<template>
  <div class="mb-3 col-lg-6 col-xl-4 px-2">
    <div class="px-0 check-card" :class="$parent.value === value ? 'active' : ''" @click="select">
        <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
    props: ["value", "disabled"],
    mounted() {
        if (this.$parent.value === undefined) {
            return;
        }
        for (let i = 0; i < this.$slots.default.length; i++) {
            const node = this.$slots.default[i]
            
            if (this.disabled || this.disabled === '') {
                node.elm.setAttribute('disabled', true)
            }
        }
    },
    methods: {
        select() {
            if (this.disabled) {
                return;
            }
            this.$parent.select(this.value);
        }
    }
}
</script>

<style scoped>
.check-card > :first-child > :first-child {
    z-index: -1;
}
</style>