<template>
  <div class="card-box-wrapper">
    <div class="card out-card" :class="`bg-${color || 'primary'}`" @click="$emit('click')" ref="card">
      <div v-if="!type || type === 'normal'" class="card card-box-normal">
        <div class="card-body p-3 d-flex align-items-center">
          <span :class="`b-avatar badge-light-${color || 'primary'} rounded-circle`" style="width: 60px; height: 60px" v-if="$slots.icon">
            <span class="b-avatar-custom">
              <slot name="icon"></slot>
            </span>
          </span>
          <div class="truncate ms-3 appcard-textarea">
            <div class="d-flex align-items-center flex-wrap">
              <slot></slot>
            </div>
            <div class="d-none d-sm-block">
              <slot name="description"></slot>
            </div>
          </div>
          <div class="appcard-btnarea">
            <slot name="btnarea"></slot>
          </div>
        </div>
      </div>
      <div v-if="type === 'box'" class="card card-box text-center">
        <div class="card-body">
          <span class="b-avatar mb-3 rounded-circle" :class="`badge-light-${color || 'primary'}`" style="width: 60px; height: 60px" v-if="$slots.icon">
            <span class="b-avatar-custom">
              <slot name="icon"></slot>
            </span>
          </span>
          <div class="truncate">
            <slot></slot>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Card",
  props: ["color", "top", "left", "right", "bottom", "type"],
  mounted() {
    if (this.top === undefined && this.left === undefined && this.right === undefined && this.bottom === undefined) {
      this.$refs.card.style.paddingTop = ".25rem";
      return;
    }

    if (this.top !== undefined) {
      this.$refs.card.style.paddingTop = (this.top === '' || this.top === true) ? ".25rem" : this.top;
    } else if (this.left !== undefined) {
      this.$refs.card.style.paddingLeft = (this.left === '' || this.left === true) ? ".25rem" : this.left;
    } else if (this.right !== undefined) {
      this.$refs.card.style.paddingRight = (this.right === '' || this.right === true) ? ".25rem" : this.right;
    } else if (this.bottom !== undefined) {
      this.$refs.card.style.paddingBottom = (this.bottom === '' || this.bottom === true) ? ".25rem" : this.bottom;
    }
  },
};
</script>

<style lang="less" scoped>
.out-card-wrapper .card {
  transition: all 5s ease;
}
.out-card {
  display: inline-block;
  padding: 0;
  // padding-top: .25rem;
  box-shadow: none;
  cursor: pointer;
  width: 100%;

  .card {
    box-shadow: 0 4px 24px 0 rgba(34, 41, 47, 0.1);
    background: #fff;
    opacity: 1;
  }
}

.appcard-textarea {
  flex-grow: 1;
}
.appcard-btnarea {
  text-align: right;
}

@media screen and (max-width: 400px) {
  .card-box-normal .b-avatar {
    display: none !important;
  }
}
</style>
