<script>
export default {
    data() {
        return {
            select: {
                auto_renew_value: 1,
                discount_value: 0
            },
            price: '',
            desc_price: '',
            desc_more_price: '',
        }
    },
    watch: {
        select: {
            immediate: true,
            deep: true,
            async handler() {
                if (this.computeFun) {
                    this.price = '';
                    const price = await this.computeFun(this.select.auto_renew_value, this.select.discount_value);
                    if (typeof price == 'number' || typeof price == 'string') {
                        this.price = price
                    } else if (typeof price == 'object') {
                        this.price = price.price
                        this.desc_price = price.desc_price
                        this.desc_more_price = price.desc_more_price
                    }
                } 
            }
        }
    },
    props: ["loading", "discount", "computeFun", "finishFun"],
}
</script>


